import React, { Component } from 'react';
import {
  Link
} from 'react-router-dom';
import '../vendor/bootstrap.min.js';

/* Header component */
class Header extends Component {
  constructor() {
    super()
    this.signOut = this.signOut.bind(this)
  }

  signOut = () => {
    throw new Error('Unable to sign out!') // fake sign out
  }

  render() {
    return (
      <nav className="navbar navbar-inverse navbar-fixed-top" role="navigation">
        <div className="container">
            <div className="navbar-header">
                <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#navbar-collapse">
                    <span className="sr-only">Toggle navigation</span>
                    <span className="icon-bar"></span>
                    <span className="icon-bar"></span>
                    <span className="icon-bar"></span>
                </button>
               <Link to="/examples"><span className="navbar-brand">BinaryOptions</span></Link>
            </div>
            <div className="collapse navbar-collapse" id="navbar-collapse">
                <ul className="nav navbar-nav">
                  <li><Link to="/examples/markets">Markets</Link></li>
                  <li><Link to="/examples/settings">Settings</Link></li>
                  <li><Link to="/examples/trading-history">Trading history</Link></li>
                  <li><Link to="/examples/contact"> Contact Us </Link></li>
                </ul>
            </div>
        </div>
      </nav>
    )
  }
}

export default Header



