import React from 'react';
import {
  Link
} from 'react-router-dom';

/* Footer component */
function Footer() {
  return (
    <div className="app-footer">
      <footer className="container">
      <span className="pull-left"> &copy; 2017 BinaryOptions. All rights reserved. </span> 
      <span className="pull-right"><Link to="/examples/contact">Terms</Link></span> 
      </footer>
    </div>
  )
}

export default Footer



