/* eslint-disable */
import $ from 'jquery';
import React, { Component }
from 'react';

import homeBg from '../images/home-bg.jpg';

/* Settings page */
class Settings extends Component {
  render() {
    return (
      <div >
        <div style={{backgroundImage: `url(${homeBg})`}} className="intro-header">
          <div className="site-heading">
          <h1>Settings</h1>
          <hr className="small" />
          <span className="subheading">Manage your account settings</span>
          </div>
        </div>
        <div className="container">
          <ul className="nav nav-tabs" role="tablist">
            <li className="nav-item">
              <a className="nav-link active" data-toggle="tab" href="#profile">Profile</a>
            </li>
            <li className="nav-item">
              <a className="nav-link" data-toggle="tab" href="#security">Security</a>
            </li>
          </ul>
          <div className="tab-content">
            <div className="tab-pane active" id="profile" role="tabpanel">
              <h2 className="section-heading">Profile settings</h2>
              <ProfileForm />
            </div>

            <div className="tab-pane" id="security" role="tabpanel">
              <h2 className="section-heading">Security settings</h2>
              <SecurityForm />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

/* Profile form component */
class ProfileForm extends Component {
  constructor(props) {
    super(props)
    this.state = {
      fname: '',
      lname: '',
      dob: '',
      address: '',
      city: '',
      state: '',
      zip: '',
      tel: '',
      email: '',
      website: ''
    }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  handleSubmit = (e) => {
    if (this.state.fname === '' ||
      this.state.lname === '' ||
      this.state.dob === '') {
      return true
    } else if (this.state.dob) {
      e.preventDefault()
      console.log(this.state.dob)
    } else {
      e.preventDefault()
      $('#profile-form-msg-').text('Your profile has been updated.')
    }
  }

  onChange = (e) => {
    switch (e.target.id) {
      case 'fname':
        this.setState({ fname: e.target.value })
        break;
      case 'lname':
        this.setState({ lname: e.target.value })
        break;
      case 'dob':
        this.setState({ dob: e.target.value })
        break;
      case 'address':
        this.setState({ address: e.target.value })
        break;
      case 'city':
        this.setState({ city: e.target.value })
        break;
      case 'state':
        this.setState({ state: e.target.value })
        break;
      case 'zip':
        this.setState({ zip: e.target.value })
        break;
      case 'tel':
        this.setState({ tel: e.target.value })
        break;
      case 'email':
        this.setState({ email: e.target.value })
        break;
      case 'website':
        this.setState({ website: e.target.value })
        break;
      default:
        break;
    }
  }

  render() {
    return (
      <form id="update-profile-form">
        <div className="col-sm-12 col-lg-8 form">
          <div className="row">
            <div className="col-sm-2 form-group">
              <label>Salutation</label>
              <div className="dropdown">
                <button className="form-control dropdown-toggle" type="button" id="dropdownMenu1" data-toggle="dropdown" aria-haspopup="true" aria-expanded="true">
                  Select
                  <span className="caret"></span>
                </button>
                <ul className="dropdown-menu" aria-labelledby="dropdownMenu1">
                  <li><a href="#">Mr</a></li>
                  <li><a href="#">Mrs</a></li>
                  <li><a href="#">Miss</a></li>
                </ul>
              </div>
            </div>
            <div className="col-sm-5 form-group">
              <label>First name</label>
              <input type="text" id="fname" placeholder="Jane" className="form-control" value={this.state.fname} onChange={this.onChange} required />
            </div>
            <div className="col-sm-5 form-group">
              <label>Last Name</label>
              <input type="text" id="lname" placeholder="Doe" className="form-control" value={this.state.lname} onChange={this.onChange} required />
            </div>
          </div>
          <div className="form-group col-xs-12 ">
            <label>Date of birth</label>
            <input className="form-control" id="dob" type="date" required />
          </div>         
          <div className="form-group">
            <label>Address</label>
            <textarea placeholder="" id="address" rows="3" className="form-control" value={this.state.address} onChange={this.onChange} ></textarea>
          </div>  
          <div className="row">
            <div className="col-sm-4 form-group">
              <label>City</label>
              <input type="text" id="city" placeholder="" className="form-control" required value={this.state.city} onChange={this.onChange}  />
            </div>  
            <div className="col-sm-4 form-group">
              <label>State</label>
              <input type="text" id="state" placeholder="" className="form-control" value={this.state.state} onChange={this.onChange} required />
            </div>  
            <div className="col-sm-4 form-group">
              <label>Zip</label>
              <input type="text" id="zip" placeholder="" className="form-control" value={this.state.zip} onChange={this.onChange}  />
            </div>    
          </div>           
          <div className="form-group">
            <label>Phone Number</label>
            <input type="tel" id="tel" placeholder="xxxx-xxx-xxxx" className="form-control" required value={this.state.tel} onChange={this.onChange}  pattern="^\d{4}-\d{3}-\d{4}$" />
          </div>    
          <div className="form-group">
            <label>Email Address</label>
            <input type="email" id="email" placeholder="" className="form-control" value={this.state.email} onChange={this.onChange} required />
          </div>  
          <div className="form-group">
            <label>Website</label>
            <input type="text" id="website" placeholder="" className="form-control" value={this.state.website} onChange={this.onChange}  />
          </div>
          <p id="profile-form-msg" className=""></p> 
          <button type="button" className="btn btn-default" onClick={this.handleSubmit}>Update profile</button>        
          </div>
      </form>
    )
  }
}

/* Security form component */
class SecurityForm extends Component {
  constructor(props) {
    super(props)
    this.state = {
      password: '',
      repassword: '',
      number: ''
    }
    this.onInvalid = this.onInvalid.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  handleSubmit = (e) => {
    if (this.state.password === '' ||
      this.state.repassword === '' ||
      this.state.number === '' ||
      this.state.repassword.length < 3) {
      return true
    } else {
      e.preventDefault()
      if (this.state.password !== this.state.repassword) {
        $('#change-password-form-msg').text("Passwords Don't Match");
        console.error('Password dont match')
        return
      } else if (this.state.number !== '13') {
        $('#change-password-form-msg').text('Oops! 5 + 8 is not ' + this.state.number)
        console.error('Oops! 5 + 8 is not ' + this.state.number)
        return
      } else {
        $('#change-password-form-msg').text('Your password has been updated.')
        this.setState = ({
          password: '',
          repassword: '',
          number: ''
        })
        return
      }
    }
  }

  onChange = (e) => {
    switch (e.target.id) {
      case 'password':
        this.setState({ password: e.target.value })
        break;
      case 'repeat':
        this.setState({ repassword: e.target.value })
        break;
      case 'number':
        this.setState({ number: e.target.value })
        break;
      default:
        break;
    }
  }

  onInvalid = (e) => {
    switch (e.target.id) {
      case 'password':
        $('#password')[0].setCustomValidity('3 characters minimum')
        break;
      case 'repeat':
        $('#repeat')[0].setCustomValidity('3 characters minimum')
        break;
      default:
        break;
    }
  }

  render() {
    return (
      <form id="change-password-form">
        <div className="col-sm-12 col-lg-4 form">
          <div className="row">
            <div className="form-group">
            <label>New password</label>
            <input type="password" id="password" placeholder="New password" pattern=".{3,}" value={this.state.password} onChange={this.onChange} onInvalid={this.onInvalid} className="form-control" required />
          </div>    
          <div className="form-group">
            <label>Confirm new password</label>
            <input type="password" id="repeat" placeholder="Repeat new password" pattern=".{4,}" onInvalid={this.onInvalid} value={this.state.repassword} onChange={this.onChange} className="form-control" required />
          </div>  
          <div className="form-group">
            <label>Verify you are a human</label>
            <input type="number" id="number" placeholder="5 + 8 = ?" className="form-control" onChange={this.onChange} value={this.state.number} required />
          </div>
          <p id="change-password-form-msg" className=""></p>
          <button type="submit" className="btn btn-default" onClick={this.handleSubmit}>Update password</button> 
          </div>
        </div>
      </form>
    )
  }
}

export default Settings



