import React from 'react';
import aboutBg from '../images/about-bg.jpg';

/* 404 page */ 
function NotFound() {
  return (
    <div>
      <div style={{backgroundImage: `url(${aboutBg})`}} className="intro-header">
      	<div className="site-heading">
          <h1>Page not found</h1>
          <hr className="small" />
          <span className="subheading">Perhaps this page was lost at the sea.</span>
          </div>
        </div>
        <div className="container">
        <h1 className="section-heading">404</h1>
        <p>Page not found.</p>
        <hr />
        </div>
    </div>
  )
}

export default NotFound


